
import processBar from '@/components/process-bar/process-bar'
import { mapGetters, mapMutations } from 'vuex'
import { toggleFavorite } from '@/common/js/mixin'
import Toast from 'vux/src/components/toast/index.vue'

export default {
mixins: [toggleFavorite],
data () {
return {
audio: {},
canPlay: false,
currentTime: 0,
percent: 0,
errorVisiable: false
}
},
methods: {
...mapMutations({
setFullScreen: 'setFullScreen',
setPlayingState: 'setPlayingState',
setCurrentIndex: 'setCurrentIndex',
setListenedList: 'setListenedList'
}),
minimize () {
this.setFullScreen(false)
},
maximize () {
this.setFullScreen(true)
},
toggglePlaying () {
let hasError = false
try {
this.playingState ? this.audio.play() : this.audio.pause()
} catch (err) {
hasError = true
} finally {
if (hasError) {
this.setPlayingState(false)
} else {
this.setPlayingState(!this.playingState)
}
}
},
prev () {
if (this.canPlay) {
let index = this.currentIndex - 1
index < 0
? this.setCurrentIndex(this.show.audioList.length - 1)
: this.setCurrentIndex(index)
}
this.canPlay = false
this.setPlayingState(true)
},
next () {
if (this.canPlay) {
let index = this.currentIndex + 1
index === this.show.audioList.length
? this.setCurrentIndex(0)
: this.setCurrentIndex(index)
}
this.canPlay = false
this.setPlayingState(true)
},
end () {
this.next()
},
updateTime (event) {
this.currentTime = event.target.currentTime
this.percent = Math.floor(
this.currentTime / this.currentAudio.duration * 100
)
if (!this.audio.currentTime) {
this.audio = event.target
}
},
error () {
this.canPlay = true
},
changeProgress (percent) {
this.percent = percent
let time = this.currentAudio.duration
let currentTime = time * (percent / 100)
this.audio.currentTime = currentTime
},
ready () {
this.canPlay = true
if (this.playingState) {
this.audio.play()
}
},
handleAudioError () {
this.setPlayingState(false)
this.errorVisiable = true
}
},
computed: {
...mapGetters([
'fullScreen',
'playingState',
'show',
'currentAudio',
'currentIndex',
'listenedList'
])
},
watch: {
currentAudio (newval, oldval) {
if (newval.audioUrl) {
this.audio = this.$refs.audio
}
},
playingState (newval) {

if (newval) {
if (this.canPlay) {
this.audio.play()
}
let isRepeat = this.listenedList.find((item, index) => {
return item.id === this.show.id
})
if (!isRepeat) {
this.setListenedList(this.show)
}
} else {
this.audio.pause()
}
},
show (newval, oldval) {
if (newval.id !== oldval.id) {

this.setCurrentIndex(0)
}

if (this.playingState) {
let isRepeat = this.listenedList.find((item, index) => {
return item.id === this.show.id
})
if (!isRepeat) {
this.setListenedList(this.show)
}
}
}
},
components: {
processBar,
Toast
}
}



